const rawSource = "import React, { useState } from \"react\";\r\nimport { Avatar, Typography } from \"@material-ui/core\";\r\nimport MultiPicker from \"../../src/index\";\r\nimport { getSuggestedFruitSync, ALL_FRUITS } from \"./common\";\r\n\r\nconst NUMBER_OF_FRUITS_TO_SHOW = 3;\r\n\r\nconst fruitAvatars = item => <Avatar alt={ item.name } src={ item.image } />;\r\n\r\nconst fruitPopover = item => (\r\n    <div style={ { display: \"flex\", alignItems: \"center\", padding: \"4px 8px\" } }>\r\n        <img src={ item.image } style={ { height: \"20px\", width: \"20px\" } } />\r\n        <Typography variant=\'subtitle1\'>{ item.detail }</Typography>\r\n    </div>\r\n);\r\n\r\nconst fruitNameWithStock = fruit => `${fruit.name} (${fruit.stock})`;\r\n\r\nexport default function BasicDemo() {\r\n    const [items, setItems] = useState(ALL_FRUITS.slice(0, NUMBER_OF_FRUITS_TO_SHOW));\r\n    return (\r\n        <MultiPicker\r\n            value={ items }\r\n            onChange={ setItems }\r\n            itemToString={ fruit => fruit.name }\r\n            getSuggestedItems={ getSuggestedFruitSync }\r\n            chipColor=\'primary\'\r\n            itemToAvatar={ fruitAvatars }\r\n            itemToLabel={ fruitNameWithStock }\r\n            itemToPopover={ fruitPopover }\r\n            label=\"Your favourite fruit\"\r\n            fullWidth\r\n        />\r\n    );\r\n}\r\n";
const markdown = "The chip elements that represent selected items allow the following customisations:\r\n * with the `itemToLabel` prop, you can override the default chip labels (which are obtained using `itemToString`)\r\n * with the `chipColor` prop, you can specify a theme color to use\r\n * with the `itemToAvatar` prop, you can use [Material Avatars](https://material-ui.com/demos/avatars/) to display per-item icons\r\n * with the `itemToPopover` prop, you can provide per-item popover helpers that activate when the user hovers over the chip";
