const rawSource = "import React, { useState } from \"react\";\r\nimport { Typography } from \"@material-ui/core\";\r\nimport MultiPicker from \"../../src/index\";\r\nimport { getSuggestedFruitSync } from \"./common\";\r\nimport Highlighter from \"react-highlight-words\";\r\nimport { object, bool, string } from \"prop-types\";\r\n\r\nfunction SuggestionWithStockNumbers({ item, isHighlighted, inputValue }) {\r\n    const style = {\r\n        display: \"flex\",\r\n        backgroundColor: isHighlighted ? \"#aaa\" : \"#fff\",\r\n        width: \"100%\"\r\n    };\r\n    return (\r\n        <div style={ style }>\r\n            <img src={ item.image } style={ { height: \"42px\", width: \"42px\" } } />\r\n            <div style={ { flex: \"1 1 0\"} }>\r\n                <Typography variant=\"h6\">\r\n                    <Highlighter\r\n                        highlightStyle={ { backgroundColor: \"#ff2\" } }\r\n                        searchWords={ [ inputValue ] }\r\n                        textToHighlight={ item.name }\r\n                    />\r\n                    <small>&nbsp;({ item.stock } in stock)</small>\r\n                </Typography>\r\n                <Typography>{ item.detail }</Typography>\r\n            </div>\r\n        </div>\r\n    );\r\n}\r\n\r\nSuggestionWithStockNumbers.propTypes = {\r\n    item: object.isRequired,\r\n    isHighlighted: bool,\r\n    inputValue: string.isRequired\r\n};\r\n\r\nexport default function BasicDemo() {\r\n    const [items, setItems] = useState([]);\r\n    return (\r\n        <MultiPicker\r\n            value={ items }\r\n            onChange={ setItems }\r\n            itemToString={ fruit => fruit.name }\r\n            getSuggestedItems={ getSuggestedFruitSync }\r\n            SuggestionComponent={ SuggestionWithStockNumbers }\r\n            label=\"Your favourite fruit\"\r\n            fullWidth\r\n        />\r\n    );\r\n}\r\n\r\n\r\n";
const markdown = "The default suggestion component just displays the id of the item (extracted with `itemToString()`) in a plain format. You can supply a React component as the `SuggestionComponent` prop, which will have access to the following props:\r\n\r\n| Prop name | Type | Description |\r\n| --------- | ---- | ----------- |\r\n| `itemId` | string | The unique ID of the item (from `itemToString`) |\r\n| `item` | any | The suggestion generated by your `getSuggestedItems` function |\r\n| `isHighlighted` | boolean | `true` if the user is currently highlighting this suggestion (either with keyboard navigation, or by hovering over with the mouse) |\r\n| `inputValue` | string | The string currently entered in the text input field. |\r\n\r\nThis can be good for emphasising search text and providing more information about the suggestion.\r\n";
